<script>
  // import cookie from 'js-cookie'
  import {mapActions, mapState} from 'vuex'

  export default {
    name: "myWallet-unlock",

    data() {
      return {
        symbol: '',
        page: 1
      }
    },
    created() {
      // if (!this.address) {
      // }
      this.getAccountState().then(data => {
        this.symbol = data.symbol
        this.getTransferList()
      }).catch(err => {
        this.$router.push('/openWallet')
      })
    },
    methods: {
      ...mapActions([
        'getAccountState',
        'getTransferList'
      ]),
      closeWallet() {
        this.$router.push('/openWallet')
      }
    },
    computed: {
      ...mapState([
        'address',
        'balance',
        'transferList'
      ])
    }
  }
</script>

